.animated {
  transition: opacity 1.2s, transform 1.2s;
}
.animated.to-right {
  transform: translateX(-200px);
  opacity: 0;
}
.animated.to-left {
  transform: translateX(200px);
  opacity: 0;
}
.animated.to-top {
  transform: translateY(300px);
  opacity: 0;
}
.animated.to-bottom {
  transform: translateY(-200px);
  opacity: 0;
}
.animated.fade-in {
  opacity: 0;
}

@keyframes to-left {
  0% {
    opacity: 0;
    transform: translateX(150px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes to-right {
  0% {
    opacity: 0;
    transform: translateX(-150px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes to-top {
  0% {
    opacity: 0;
    transform: translateY(300px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes to-bottom {
  0% {
    opacity: 0;
    transform: translateY(-150px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.animated-sync-top {
  transform: translateY(200px);
